@import '../../assets/fonts/fonts';

html {
  scroll-behavior: smooth;
}

.wrapper {
  margin: 0 auto;
  width: 1150px;

  @media screen and (max-width: 1280px) {
    width: 930px;
  }

  @media screen and (max-width: 992px) {
    width: 768px;
  }

  @media screen and (max-width: 768px) {
    width: 567px;
  }

  @media screen and (max-width: 576px) {
    width: 95%;
  }
}
